<template>
    <v-layout fluid>
        <v-card width="100%">
            <v-card-title class="justify-center">Información General del Proponente</v-card-title>
            <v-card-text>
              <v-row>
                    <v-col cols="12">
                      <span class="h2 font-weight-bold black--text">{{ `Estado: ${estadotxt}` }}</span>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <v-select
                            v-model="naturalezajuridica"
                            :items="dataNaturaleza"
                            label="Naturaleza Jurídica"
                            item-text="text"
                            item-value="value"
                            hide-details
                            single-line
                            outlined
                            rounded
                            dense
                            :cache-items="false"
                        >
                        </v-select>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12" v-if = "naturalezajuridica == 'N'">
                        <natural
                            v-if = "naturalezajuridica == 'N'"
                            :codigo.sync = 'codigo'
                            :email.sync = 'email'
                            :dataPrefijos.sync = 'dataPrefijos'
                            :dataPaises.sync = 'dataPaises'
                            :dataCiudades.sync = 'dataCiudades'
                            :dataRegistro.sync = 'dataRegistro'
                        />
                    </v-col>
                    <v-col cols="12" v-if = "naturalezajuridica == 'J'">
                        <juridica
                            v-if = "naturalezajuridica == 'J'"
                            :email.sync = 'email'
                            :codigo.sync = 'codigo'
                            :dataPrefijos.sync = 'dataPrefijos'
                            :dataPaises.sync = 'dataPaises'
                            :dataCiudades.sync = 'dataCiudades'
                            :dataTipoEmpresa.sync = 'dataTipoEmpresa'
                            :dataRegistro.sync = 'dataRegistro'
                        />
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
    </v-layout>
</template>

<script>
import { registroProponentes } from '@/services/api'

export default {
  name: 'regGeneral',
  props: ['email', 'codigo', 'dataRegistro'],
  components: {
    natural: () => import('../registro/regPersonaNatural'),
    juridica: () => import('../registro/regPersonaJuridica')
  },
  data: () => ({
    naturalezajuridica: '',
    estadotxt: '',
    dataPrefijos: [],
    dataTipoEmpresa: [],
    dataPaises: [],
    dataCiudades: [],
    dataNaturaleza: [
      { value: 'N', text: 'PERSONA NATURAL' },
      { value: 'J', text: 'PERSONA JURÍDICA' }
    ]
  }),
  methods: {
    async getPaises () {
      try {
        const { data } = await registroProponentes.getPaises()
        this.dataPaises = data.data
      } catch (error) {
      } finally {
      }
    },
    async getCiudades () {
      try {
        const { data } = await registroProponentes.getCiudades()
        this.dataCiudades = data.data
      } catch (error) {
      } finally {
      }
    },
    async getPrefijos () {
      try {
        const { data } = await registroProponentes.getPrefijosTelefonos()
        this.dataPrefijos = data.data
      } catch (error) {
      } finally {
      }
    },
    async getTipoEmpresa () {
      try {
        const { data } = await registroProponentes.getTiposEmpresa()
        this.dataTipoEmpresa = data.data
      } catch (error) {
      } finally {
      }
    }
  },
  watch: {
    dataRegistro: function (val) {
      if (val) {
        this.naturalezajuridica = this.dataRegistro.naturalezajuridica
        this.estadotxt = this.dataRegistro.estadotxt
      }
    }
  },
  mounted () {
    this.getPaises()
    this.getCiudades()
    this.getPrefijos()
    this.getTipoEmpresa()
    if (this.dataRegistro) {
      this.naturalezajuridica = this.dataRegistro.naturalezajuridica
      this.estadotxt = this.dataRegistro.estadotxt
    }
  }
}
</script>
